.circle {
  width: 280px;
  height: 280px;
  margin: 0 auto;
  position: relative;
  border: 4px solid white;
  border-radius: 50%;
}

.circle .face {
  width: 100%;
  height: 100%;
}

.circle .face:after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12px;
  height: 12px;
  margin: -6px 0 0 -6px;
  background: white;
  border-radius: 6px;
  content: "";
  display: block;
}

.circle .face .hour, .circle .face .minute, .circle .face .second, .circle .face .millisecond {
  width: 0;
  height: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  background: white;
  border-radius: 4px;
}

.circle .face .minute, .circle .face .second {
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
}


.circle .face .hour {
  margin: -2px 0 -2px -25%;
  padding: 4px 0 1px 25%;
  -webkit-transform-origin: 101.5% 50%;
          transform-origin: 101.5% 50%;
}

.circle .face .millisecond {
  margin: -0.5px 0 0.5px -15%;
  padding: 0.5px 0 0.5px 15%;
  -webkit-transform-origin: 50%;
          transform-origin: 50%;
  background: white ;

}
.circle .face .minute {
  margin: -40% -2px 0;
  padding: 40% 2px 0;
}

.circle .face .second {
  margin: -40% -1px 0 0;
  padding: 40% 1px 0;
}
.second {
  margin: -42% -1px 0 0!important;
  padding: 42% 1px 0!important;
}
.minute {
  margin: -40% -1px 0!important;
  padding: 40% 2px 0!important;
}

